.previewer-education {
  grid-column: 1 / 3;
}

.previewer-education > ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 1rem;
  row-gap: 1.5rem;
}

@media only screen and (max-width: 450px) {
  .previewer-education > ul {
    grid-template-columns: repeat(1, 1fr);
  }
}
